@font-face {
    font-family: 'Gotham Pro';
    src: url('../fonts/GothamPro.eot');
    src: url('../fonts/GothamPro.eot?#iefix') format('embedded-opentype'),
        url('../fonts/GothamPro.woff2') format('woff2'),
        url('../fonts/GothamPro.woff') format('woff'),
        url('../fonts/GothamPro.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
body {
margin: 0 auto;
}

.container.container__main {
    max-width: 1100px;
    padding-right: 0;
  padding-left: 0;
}
.header {
	font-family: 'Gotham Pro';
	text-transform: uppercase;
	white-space: nowrap;
	font-size: 100%;
}
.header__nav {
	margin: 0;
	padding: 0;
	text-align: center;
}
.header__nav-item {
	list-style: none;
	margin-top: 29px;
}
.header__nav-link {
	text-decoration: none;
	color: #96a6a7;
	font-size: 14px;
	line-height: 29px;
	&:hover {
		text-decoration: none;
	}
}
.header__login {
	background-color: red;
	display: flex;
    height: 100%;
    &::after {
        content: "";
        display: block;
        width: 9999px;
        height: 100%;
        position: absolute;
        top: 0;
        right: -9990px;
        background-color: red;
    }
}
.header__logo {
	margin-top: 18px;
	text-align: center;
}
.header__search {
	background-color: #e2dee0;
    display: flex; 
    justify-content: center;
    height: 100%; 
    &-link {
        margin: auto;
        display: flex;
        &:hover {
            text-decoration: none;
        }
    }
}
.header__login {
    display: flex;
    height: 100%; }
.fa-search {
	color: white;
	margin: auto;
}

.fa-user, .header__login-text {
    margin: auto;
    color: #ffffff; }
.header__login-text {
	font-size: 14px;
	line-height: 29px;
	margin-left: 12px;
}
.header__searchform {
	background: url(../images/background_image.png) center/cover no-repeat;;
	height: 500px;
	font-family: 'Gotham Pro';
	color: white;
    padding-top: 58px;
    &-title {
        text-align: center;
        text-shadow: 0 1px 1px rgba(0, 0, 1, 0.15);
        font-size: 30px;
        line-height: 48px;
    }
}


.header__searchform-btn, .example {
	width: 220px;
	height: 50px;
	border: 1px solid rgba(255, 255, 255, 0.2);
	background-color: rgba(255, 255, 255, 0.1);
	border-radius: 35px;
	padding-left: 30px;
	padding-right: 24px;
	text-decoration: none;
	margin-top: 33px;
	margin-bottom: 23px;
	&:not(:last-of-type) {
		margin-right: 29px;
	}
}

.select2-container--focus {
background-color: red;
}

.select2-selection__rendered {
    color: red;

}

#select2-state-f2-container {
	background-color:red;
}

.select2-container--open {
	border: 1px solid rgba(255, 255, 255, 0.2);
	background-color: rgba(255, 255, 255, 0.1);
	border-radius: 35px;
	padding-left: 30px;
	padding-right: 24px;
}

.select2-container--default.select2-container--open {
    color: red;
    list-style: none;
    font-size: 30px;
    list-style: none; 
    }
.searchform-btn__link {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	align-items: center;
	color: white;
	cursor: pointer;
	// font-family: $font_1;
	font-size: 18px;
	line-height: 30px;
	padding-top: 10px;
}

.main__wrap .container.container__main {
	max-width: 945px;
}

.description__wrapper {
			border-left: 1px solid #e2dee0;
border-right: 1px solid #e2dee0;
border-bottom: 1px solid #e2dee0;
    border-radius: 6px;
}
.cardbox__card {
	padding-top: 46px;
	width: 100%;
	font-family: 'Gotham Pro';
	box-shadow: 0 1px 0 #e2dee0;
	padding-bottom: 32px;
	&-description {
		margin: 0 15px 0 15px;
		margin-top: 35px;

		&--title {
	color: #566375;
	font-size: 14px;
	line-height: 18px;
	text-align: center;
	    min-height: 35px;
		}
		&--details {
		color: #1baf5d;
		font-family: 'Gotham Pro';
		font-size: 12px;
		line-height: 48px;
		text-transform: uppercase;
		text-align: center;
		}
	}

}
.cardbox__card-description--rating {
	display: flex;
    justify-content: center;
}

.cardbox__card-description--comments {
	color: #96a6a7;
	font-size: 12px;
    line-height: 11px;
}


.cardbox__card-description--rate {
	display: flex;
	font-size: 10px;
	margin-bottom: 15px;
	margin-right: 10px;
}

.cardbox__card-image {
	border-bottom: 3px solid #1baf5d;
	position: relative;
	background: url(../images/layer-6.png) center/contain no-repeat;
	min-height: 140px;


	&::before {
	    content: '$2,990';
	    font-size: 12px;
	    text-align: center;
	    padding-top: 5px;
	    width: 65px;
	    height: 25px;
	    background-color: #1baf5d;
	    display: block;
	    position: absolute;
    	right: 3%;
	    top: -7px;
	    border-radius: 50px;
	    color: white;
	    margin-top: 14px;
	}
	&::after {
	width: 55px;
	height: 55px;
	border: 3px solid #ffffff;
	background-color: #1baf5d;
	content: '';
	display: block;
	border-radius: 50%;
	position: absolute;
	left: 38%;
	bottom: -27px;
	background-image: url(../images/vector-smart-object-copy.png);
	background-repeat: no-repeat;
    background-position: center;
	}
}

.cardbox__card-second .cardbox__card-image {
	border-bottom: 3px solid #f19e1f;
	background: url(../images/layer-7.png) center/contain no-repeat;
	min-height: 140px;
	&::before {
	display: none;

	}
	&::after {
	background-color: #f19e1f;
	background-image: url(../images/vector-smart-object-copy-5.png);
	}
}

.cardbox__card-second .cardbox__card-description--details {
	color: #f19e1f;
}

.cardbox__card-third .cardbox__card-image {
	border-bottom: 3px solid #88499c;
	background: url(../images/layer-6.png) center/contain no-repeat;
	&::before {
	display: none;
	}
	&::after {
	background-color: #88499c;
	background-image: url(../images/vector-smart-object-copy-10.png);
	}
}

.cardbox__card-fourth .cardbox__card-image {
	background: url(../images/layer-8.png) center/contain no-repeat;
	}

.cardbox__card-third .cardbox__card-description--details {
	color: #88499c;
}

.card__middle .cardbox__card-image {
	background: url(../images/layer-9.png) center/cover no-repeat;
	&::before {
	display: none;
	}
	&::after {
	left: 5%;
	}
	}

.cover__bg,
.cover__text {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background-color: rgba(27, 175, 93, 0.7);
}

.cardbox__card-second .cover__bg,
.cardbox__card-second .cover__text {
	 background-color: rgba(241, 158, 31, 0.7);
}
.cardbox__card-second .cover__text--button {
	background-color: #f19e1f;
}

.cardbox__card-third .cover__bg,
.cardbox__card-third .cover__text {
	 background-color: rgba(136, 73, 156, 0.7);
}
.cardbox__card-third .cover__text--button {
	background-color: #88499c;
}





.cardbox__card-first:hover .cover__bg,
.cardbox__card-first:hover .cover__text,
.cardbox__card-second:hover .cover__bg,
.cardbox__card-second:hover .cover__text,
.cardbox__card-third:hover .cover__bg,
.cardbox__card-third:hover .cover__text,
.cardbox__card-fourth:hover .cover__bg,
.cardbox__card-fourth:hover .cover__text{
  display: block;
}

.cover__text--link {
	padding-left: 18px;
	padding-right: 8px;
	font-size: 12px;
	color: #ffffff;
	font-family: 'Gotham Pro';
	line-height: 33px;
	text-transform: uppercase;
}
.row__forflex {
	display: flex;
	justify-content: space-around;
}

.cover__text--rate {
		font-size: 12px;
}

.cover__text--button {
	width: 137px;
	height: 31px;
	border: 2px solid #ffffff;
	background-color: #1baf5d;
	border-radius: 50px;
	margin-top: 25px;
	margin-left: 38px;
}

.cover__text--rate {
	display: flex;
	justify-content: spase-around;
	padding-top: 22px;
}





.fa-star {
	color: #f19e1f;
}

@media screen and(max-width: 768px) {
	.header__nav-item {
		margin-top: 0;
	}
	.header__searchform-btn {
		margin-top: 25px;
		&:not(:last-of-type) {
			margin-right: 0;
		}
	}
}
@media screen and(min-width: 769px) and(max-width: 992px) {
	.header__nav-item {
		margin-top: 12px;
	}

}
@media screen and(min-width: 576px) and(max-width: 767px) {
	.header__searchform-btn:not(:last-of-type) {
		margin-right: 29px;
	}

.container.container__main {
	max-width: 1100px;
}
.header__search-link:hover {
	text-decoration: none;
}
}

@media screen and (max-width: 992px) {
			.cardbox__card-image::after {
    left: 43%;
    }
    .cardbox__card-image::before {
        right: 29%;
    }
}

